/* =============
 table of contents
===============
 
1. Left Section ==> Filters
2. Right Section
3. Light State
4. Responsive Design
 
*/

main.flex {
    gap: 1.5rem;
    align-items: start;
    justify-content: start;
}

/* ===============
   1. Left Section ==> Filters
================ */


main.flex .left-section.flex {
    flex-direction: column;
    gap: 1rem;
}

main.flex .left-section.flex button {
    background-color: rgba(36, 37, 46, 1);
    width: 11rem;
    padding: 0.65rem 0;
    text-align: center;
    font-size: 1.2rem;
    opacity: 0.5;
    border-radius: 5px;
    transition: 0.3s;
}

main.flex .left-section.flex button:hover {
    opacity: 1;
}

main.flex .left-section.flex button.active {
    opacity: 1;
    font-weight: bold;
    padding: 0.8rem 0;
    letter-spacing: 1px;
    border: 1px solid var(--blue);
}


/* ===============
   2. Right Section
================ */

main.flex .right-section.flex {
    flex-wrap: wrap;
    justify-content: center;
    gap: 1.5rem;
    flex-grow: 1;
}

.card {
    border: 1px solid rgba(93, 188, 252, 0.3);
    border-radius: 10px;
    transition: 0.3s;
    /* background-color: var(--bgHeader); */
    background-image: linear-gradient(rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05));
}

.card:hover {
    border: 1px solid var(--blue);
    rotate: 2deg;
    box-shadow: 0px 2px 4px var(--blue);
    cursor: pointer;
    scale: 1.05;
}

.card img {
    border-radius: 10px 10px 0 0;
    height: 240px;
    object-fit: fill;
    transition: 0.25s;
}

.card .box {
    padding: 1rem 0.4rem;
}

.card .box .title {
    color: var(--title);
    text-transform: capitalize;
}

.card .box .sub-title {
    min-height: 100px;
    color: var(--sub-title);
    font-size: 0.85rem;
    margin: 1rem 0;
}

.card .box .icons {
    justify-content: space-between;
}

.card .box .icons a i {
    color: var(--sub-title);
    font-size: 1.2rem;
    transition: 0.3s;
}

.card .box .icons a:hover i {
    color: var(--icon-hover);
    font-size: 1.28rem;
}


.card .box .icons .link-to {
    gap: 0.3rem;
    color: var(--blue);
    font-size: medium;
}

.card .box .icons .link-to i {
    align-self: end;
}

.card .box .icons .fa-link {
    padding-right: 0.5rem;
}

/* ===============
   3. Light State
================ */

.light main.flex .left-section.flex button.active {
    border: 1px solid #000;
}

.light .card,
.light .card:hover  {
    border: none;
    box-shadow: 1px 5px 5px rgba(0, 0, 0, 0.185),-1px -5px 5px rgba(0, 0, 0, 0.185);
}


/* ===============
   4. Responsive Design
================ */

@media (max-width:1200px) {
    main.flex {
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    main.flex .left-section.flex {
        flex-direction: row;
        flex-wrap: wrap;
    }
}


@media (max-width:700px) {
    main.flex {
        flex-direction: column;
        align-items: center;
    }

    main.flex .left-section.flex {
        flex-direction: row;
        flex-wrap: wrap;
    }

    main.flex .left-section.flex button {
        width: 9rem;
    }

    main.flex .right-section.flex {
        justify-content: center;
    }
}