footer.flex {
    justify-content: space-between;
    padding-bottom: 1.5rem;
}

footer.flex ul.flex{
    gap: 0.65rem;
}

footer.flex ul.flex li a{
    color: var(--title);
    font-weight: bold;
    font-size: 0.9rem;
    transition: 0.3s;
}

footer.flex ul.flex li a:hover{
    font-size: 1rem;
    font-weight: 800;
    color: var(--blue);
}

footer.flex p.sub-title{
    color: var(--sub-title);
}

@media (max-width:800px){
    footer.flex {
        flex-direction: column;
        gap: 0.75rem;
    }
}
