/* =============
 table of contents
===============
 
1. Title Section
2. Form Section
3. Animation
4. Light State
5. Responsive Design
 
*/

/* ===============
   1. Title Section
================ */

section.contact-us .fa-envelope {
    color: var(--sub-title);
    margin-right: 1rem;
    font-size: 1.8rem;
}

section.contact-us .title {
    color: var(--title);
    font-size: 2.1rem;
    margin-bottom: 1rem;
}

section.contact-us .sub-title {
    color: var(--sub-title);
    margin-bottom: 2rem;
    line-height: 1.6rem;
    font-size: 1.1rem;
}

section.contact-us .content{
    justify-content: space-between;
}

/* ===============
   2.  Form Section
================ */

section.contact-us label {
    color: var(--sub-title);
    font-size: 1.1rem;
}

section.contact-us #email,
section.contact-us #message {
    all: unset;
    background-color: rgba(63, 63, 70, 0.15);
    border: 1px solid rgb(63, 63, 70);
    width: 18rem;
    padding: 0.5rem 1rem;
    margin-left: 1rem;
    border-radius: 8px;
    transition: 0.3s;
}

section.contact-us #email:focus,
section.contact-us #message:focus,
section.contact-us #email:hover,
section.contact-us #message:hover {
    border-color: rgb(45, 212, 191);
}

section.contact-us #message {
    min-height: 9rem;
    resize: vertical;
}

section.contact-us .submit {
    background-color: rgba(36, 37, 46, 1);
    padding: 0.5rem 0.5rem;
    display: block;
    text-align: center;
    width: 9rem;
    margin: 2rem auto 0.5rem;
    font-size: 1.35rem;
    border-radius: 8px;
    transition: 0.3s;
    border: 1px solid rgb(63, 63, 70);
    
}


section.contact-us .submit:hover {
    scale: 0.95;
    border-color: rgb(45, 212, 191);
}

.submit-message{
    color: var(--sub-title);
    margin-top: 0.75rem;
    font-size: 1.25rem;
}

/* ===============
   3. Animation
================ */

.animation{
    margin-right: 2rem;
}

.animation .contact-us-animation{
    height: 400px;
    transition: 0.5s;
    rotate: -3deg;
}

.contact-us-animation:hover{
    scale: 1.1;
}

/* ===============
   4. Light State
================ */

.light section.contact-us #email,
.light section.contact-us #message {
    border: 1px solid rgba(63, 63, 70,0.2);
    color: black;
}

/* ===============
   5. Responsive Design
================ */
@media (max-width:1200px){
section.contact-us .animation{
    display: none;
}
}

@media (max-width:725px){
    section.contact-us form{
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    section.contact-us form div.flex{
        flex-direction: column;
        align-items: start;
    }

    section.contact-us #email,
    section.contact-us #message {
        width: 100%;
        padding: 0.65rem 0;
        margin-left: 0;
        margin-top: 1rem;
    }

    section.contact-us .submit {
        width:30%;
        align-self: center;
    }
}