/* =============
 table of contents
===============
 
1. Left section
    1.1 Avatar
    1.2 Header
    1.3 Paragraph
    1.4 Icons
2. Right section ==> animation
 
*/

section.hero {
    margin-top: 3rem;
}

/* ===============
   1. Left section
================ */

section.hero .left-section {
    flex-grow: 1;
}

/* ===============
   1.1 Avatar
================ */

.left-section .parent-avatar{
    justify-content: start;
    align-items: end;
    gap: 0.5rem;
}

.left-section .avatar {
    width: 88px;
    padding: 2px;
    border-radius: 50%;
    border: 1px solid darkorange;
    box-shadow: 2px 2px 40px gray inset;
}

.left-section .fa-circle-check {
    color: var(--blue);
    margin-bottom: 0.3rem;
}

/* ===============
   1.2 Title
================ */

.left-section .title{
    color: var(--title);
    font-size: 2.5rem;
    line-height: 3rem;
    margin: 2rem 0;
}

/* ===============
   1.3 Paragraph
================ */

.left-section .sub-title{
    color: var(--sub-title);
    line-height: 1.2;
    line-height: 1.7rem;
    margin-bottom: 1rem;
}

/* ===============
   1.4 Icons
================ */

.left-section .media-buttons{
    justify-content: start;
    gap: 0.75rem;
}

.left-section .media-buttons a i{
    color: var(--sub-title);
    font-size: 1.2rem;
    transition: 0.3s;
}

.left-section .media-buttons a i:hover{
    font-size: 1.26rem; 
    color: var(--icon-hover);
}



/* ===============
   2. Right section
================ */
.right-section{
    margin-left: 2rem;

}

.hero-animation{
    height: 350px;
    width: 350px;
    transition: 0.5s;
}

.hero-animation:hover{
    scale: 1.1;
}

/* ===============
   3. Responsive
================ */

@media (max-width:1200px){
    section.hero div.right-section{
        display: none;
    }
}