/* ===============
   1. Header style
================ */

header.flex {
    justify-content: space-between;
    padding: 0 3rem;
    margin-top: 1.2rem;
}

header.flex nav {
    background-color: var(--bgHeader);
    padding: 0.8rem 1.5rem;
    border-radius: 50px;
    box-shadow: 2px 2px 5px rgba(48, 45, 45, 0.475);
}

header ul.flex {
    gap: 1rem;
}

header ul li a {
    color: var(--title);
    opacity: 0.8;
    font-size: 0.8rem;
    font-weight: 500;
    transition: 0.2s;
}

header ul li a:hover {
    color: var(--blue);
    opacity: 1;
    font-size: 0.9rem;
}

header button {
    font-size: 1.2rem;
}

header button.menu {
    display: none;
}

/* ===============
   2. IconButtons
================ */

header button.menu,
header button.theme {
    background-color: var(--bgHeader);
    height: 2.4rem;
    width: 2.4rem;
    text-align: center;
    border-radius: 50%;
    color: var(--sub-title);
    border: 1px solid rgba(235, 100, 85, 0.279);
    transition: 0.4s;
}

header button.menu:hover,
header button.theme:hover {
    color: var(--title);
    border: 1px solid rgba(235, 100, 85, 1);
}

header .fa-xmark {
    font-size: 1.3rem;
    color: var(--title);
    transition: 0.3s;
}

header .fa-xmark:hover {
    font-size: 1.6rem;
    color: crimson;
    transform: rotate(180deg);
}


/* ===============
   3. Modal "Pop up menu"
================ */

header .fixed {
    position: fixed;
    inset: 0;
    z-index: 11;
    background-color: rgba(40, 40, 48, 0.9);
    backdrop-filter: blur(3px);
}

@keyframes modalOpening {
    0% {
        scale: 0;
    }

    60% {
        scale: 1.2;
    }

    100% {
        scale: 1;
    }
}

header .fixed ul.modal {
    animation: modalOpening 0.5s 1;
    width: 75%;
    margin: 2rem auto;
    padding: 1rem 2rem;
    border-radius: 1.5rem;
    background-color: var(--secondary);
}

header .fixed ul.modal li:not(:last-child) {
    border-bottom: 1px solid var(--border-color);
}

header .fixed ul.modal li {
    padding: 0.77rem 0;
}

header .fixed ul.modal li:first-child {
    margin-bottom: -0.5rem;
    text-align: right;
    border: none;
    padding: 0.2rem 0 0;
}

header .fixed ul.modal li a {
    font-size: 1rem;
    transition: 0.3s;
}

header .fixed ul.modal li a:hover {
    padding-left: 0.5rem;
}

/* ===============
   4. Light state
================ */

.dark .fa-sun:hover{
    transition: 0.3s;
    color: rgba(255, 167, 100, 0.642);
}

.light .fa-moon:hover{
    transition: 0.3s;
    color:rgba(41, 33, 33, 0.506);
}

/* ===============
   5. Responsive Design
================ */

@media (max-width:700px) {
    header.flex nav {
        display: none;
    }

    header button.menu {
        display: block;
    }

}