/***** Google Font *****/
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');
/***** Global rules *****/

html {
  font-family: 'Roboto', sans-serif;
  box-sizing: border-box;
  letter-spacing: 0.5px;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

button {
  all: unset;
  cursor: pointer;
}

button:active {
  scale: 0.9;
}

button:disabled{
  cursor: not-allowed;
  opacity: 0.5;
}

/***** Global Variables *****/

:root {
  --primary: #000;
  --secondary: rgb(24, 24, 27);

  --border-color: rgba(63, 63, 70, 0.4);

  --title: rgb(244, 244, 245);
  --sub-title: rgb(161, 161, 170);

  --bgHeader: rgb(39, 39, 42);
  --blue: rgb(93, 188, 252);

  --icon-hover: #d4d4d8;
}

body {
  background-color: var(--primary);
  color: #fff;
  overflow-x: hidden;
}

/***** Light State *****/

.light {
  --primary: rgba(245, 245, 245, 0.657);
  --secondary: rgb(255, 255, 255);
  
  --border-color: rgba(202, 202, 202, 0.5);

  --title: rgb(39, 39, 42);
  --sub-title: rgb(82, 82, 91);

  --bgHeader: rgb(253, 253, 253);
  --blue: rgb(0, 149, 246);

  --icon-hover: #333;
}

/***** Container *****/

.container {
  width: 85%;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--secondary);
  border: solid 2px var(--border-color);
  padding: 0 3rem;
  position: relative;
}

.divider {
  border-bottom: 1px solid var(--border-color);
  margin: 3rem 0;
  width: calc(100% + 6rem);
  margin-left: -3rem;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}

.border {
  border: 2px solid red
}

/* ===============
   3. Scrollbar
================ */

::-webkit-scrollbar {
  width: 0.75rem;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2.5px 2px rgba(0, 0, 0, 0.5);
  border-radius: 8px;
  background-color: #333;
}

::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 8px;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* ===============
   3. Scroll To Top Button
================ */

.scroll2Top {
  background-color: rgba(25, 118, 210, 0.85);
  height: 2.6rem;
  width: 2.6rem;
  text-align: center;
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 50%;
  position: fixed;
  bottom: 3%;
  right: 3%;
  transition: 0.3s;
}

.scroll2Top i{
  color: #d4d4d8;
}

.scroll2Top:hover {
  background-color: rgba(25, 118, 210, 1);
}

@media (max-width:1036px) {
  .container {
    width: 85%;
    padding: 0 1.7rem;
  }

  .divider {
    width: calc(100% + 1.7rem + 1.7rem);
    margin-left: -1.7rem;
  }
}

@media (max-width:700px) {
  .container {
    width: 95%;
    padding: 0 1.5rem;
  }

  .divider {
    width: calc(100% + 3rem);
    margin-left: -1.5rem;
  }

  .scroll2Top {
    height: 2.2rem;
    width: 2.2rem;
    opacity: 0.6;
  }
}